.container{
position: relative;
max-width: 220px;
width: 100%;
height: 300px;
padding: 20px 20px 20px;
border-radius: 10px;
background-color: green;
}

.buttons{
    display: grid;
    grid-gap: 18px;
    grid-template-columns: repeat(4,1fr);
}


.display 
{
    width: 170px;
    margin-bottom: 10px;
    text-align: right;
    font-size: 20px;
}

.buttons button{
    padding: 18px 16xp;
    border-radius: 5px;
    border: none;
    font-size: 30px;
}

button.operation{
    background-color: black;
    color: white;
}

button.clear{
    background-color: red;
    color: white;
}
